 border: 1px solid red;
} */

.container {
  margin-top: 20vh;
  display: flex;
  justify-content: space-around;
  gap: 20px;
  /* flex-direction: column; */
}
/* For Mobile Portrait View */
@media screen and (max-device-width: 480px) {
  .container {
    flex-direction: column;
    margin-top: 5vh;
    justify-content: none;
    width: 100vw;
  }
}

.filler {
  border: 1px solid black;
  width: 20%;
  height: 405px;
  overflow: scroll;
  border-radius: 5px;

  background: rgba( 255, 255, 255, 0.35 );
  box-shadow: -3px 5px 32px 3px rgba(55, 55, 55, 0.221);
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .filler{
    width: 100vw;
    margin-top: 5vh;
    height: 30vw;
  }
}

.rcp-dark {
  --rcp-input-text: #000000;
  --rcp-input-border: rgba(255, 255, 255, 0.1);
  --rcp-input-label: #000000;
}

.rcp {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  /* margin: 0vh auto 0px auto; */
  background-color: var(--rcp-background);
  border-radius:10px;
}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .rcp{
    /* width: 80%; */
    display: block;
  }
}

.rcp-body {
  margin-top: 0vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  width: 100%;
  box-sizing: border-box;
  padding: 20px;
}
/* For Mobile Portrait View */
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .rcp-body{
    /* width: 80%; */
    gap: 0px;
    padding: 0px;
  }
  .rcp-dark{
    display: flex;
    justify-content: center;
  }
}

.rcp-saturation {
  position: relative;
  width: 100%;
  background-image: linear-gradient(transparent, black), linear-gradient(to right, white, transparent);
  user-select: none;

  /* background: rgba( 255, 255, 255, 0.35 ); */
  box-shadow: 0px 25px 32px 5px rgba(55, 55, 55, 0.221);
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 5px;

}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .rcp-saturation{
    width: 100vw;
  }
}

.rcp-saturation-cursor {
  position: absolute;
  
  width: 20px;
  height: 20px;
  
  border: 2px solid #ffffff;
  border-radius: 50%;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  
  transform: translate(-10px, -10px);
}

.rcp-hue {
  position: relative;
  top: -10px;
  width: 100%;
  height: 12px;
  background-image: linear-gradient(
    to right,
    rgb(255, 0, 0),
    rgb(255, 255, 0),
    rgb(0, 255, 0),
    rgb(0, 255, 255),
    rgb(0, 0, 255),
    rgb(255, 0, 255),
    rgb(255, 0, 0)
  );
  border-radius:10px;

  user-select: none
}

.rcp-hue-cursor {
  position: absolute;

  width: 20px;
  height: 20px;

  border: 2px solid #ffffff;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 0px 0.5px;
  box-sizing: border-box;

  transform: translate(-10px, -4px);
}

.rcp-alpha {
  position: relative;

  width: 100%;
  height: 12px;

  border-radius: 10px;

  user-select: none;
}

.rcp-alpha-cursor {
  position: absolute;

  width: 20px;
  height: 20px;

  border: 2px solid #ffffff;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 0px 0.5px;
  box-sizing: border-box;

  transform: translate(-10px, -4px);
}

.rcp-fields {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 10px;

  width: 100%;
}

.rcp-fields-element {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;

  width: 100%;
}

.hex-element {
  grid-row: 1;
}

.hex-element:nth-child(3n) {
  grid-column: 1 / -1;
}

.rcp-fields-element-input {
  width: 100%;
  font-size: 14px;
  font-weight: 600;
  color: black;
  text-align: center;
  background: none;
  box-sizing: border-box;
  padding: 5px;

  backdrop-filter: blur(5px) saturate(124%);
  -webkit-backdrop-filter: blur(5px) saturate(124%);
  background-color: rgba(255, 255, 255, 0.26);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.125);
  box-shadow: -3px 5px 32px 3px rgba(55, 55, 55, 0.221);
}
@media screen and (max-device-width: 480px) {
  .rcp-fields-element-input{
    background-color: #ffffff;
  }
}

.rcp-fields-element-label {
  font-size: 14px;
  font-weight: 600;

  color: var(--rcp-input-label);
  text-transform: uppercase;
}
