.colorharmony {
  width: 20vw;
  height: 40vh;
  margin: 0px;
  justify-content: center;
  padding: 0px;

  background: rgba( 255, 255, 255, 0.35 );
  box-shadow: -3px 5px 32px 3px rgba(55, 55, 55, 0.221);
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .colorharmony{
    width: 100vw;
    display: flex;
    box-shadow: none;
  }
}

.select {
  text-align: center;
  font-size: 1.2rem;
  width: 100%;
  margin: 0px;
  margin-bottom: 10px;
  margin-top: 30px;
  padding: 0px;
}

ul {
  margin: 0px;
  padding: 0px;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}

.butCont {
  width: 200px;
  margin: 2px auto;
  border-radius: 3px;
  transition: all .2s ease;
  text-align: center;
  height: 3vh;
  padding: 0px;
}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .butCont{
    border: 1px solid black;
  }
}
.butCont:hover {
  transition: all .2s ease;
  width: 230px;
  text-align: center;
}
.butCont button {
  color: inherit;
  background-color: rgba(0, 0, 0, 0);
  border: 0px;
  font-size: 1.2rem;
  margin: 0px 0px;
  width: 100%;
  text-align: center;
  transition: all .2s ease;
  height: 100%;
}
.butCont button:hover{
  transition: all .2s ease;
  cursor: grab;
  background-color: rgba(0, 0, 0, 0.055);
  text-align: center;
  font-size: 1.3rem;
  height: 100%;
  border-radius: 4px;
}
.butCont button:active{
  background-color: rgba(0, 0, 0, 0.573);
}