.colorBox {
  /* border: 1px solid red; */
  display: flex;
  margin: 5vh auto 0px auto;
  height: 200px;
  justify-content: center;
}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .colorBox{
    height: 20vw;
    /* width: 20vw; */
    margin-bottom: 200px;
  }
}

.colorBox div {
  display: inline-block;
  width: 200px;
  height: 100%;
  margin-left: 5px;
  margin-right: 5px;
  background-color: rgb(118, 118, 118);
}

.text {
  text-align: center;
  position: relative;
  bottom: -95%;
}

.star {
  display: block;
  /* border: 1px solid red; */
  width: 30px;
  height: 30px;
  fill: rgba(180, 180, 180, 0);
  transition: all .3s ease;
  position: relative;
  bottom: 0%;
  right: 0%;
}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .star{
    width: 20vw;
    height: 20vw;
  }
}

.star:hover {
  bottom: 1%;
  right: 0.0%;
  cursor: grab;
  fill: rgba(180, 180, 180, 0.204);
  width: 35px;
  height: 35px;
  transition: all .3s ease;
}
@media screen and (max-device-width: 480px)
and (orientation: portrait) {
  .star:hover{
    width: 20vw;
    height: 20vw;
  }
}

#pDiv {
  display: flex;
  margin-top: 4px;
  border-radius: 4px;
}
#pDiv:hover {
  background-color: rgba(0, 0, 0, 0.04);
  cursor: grab;
}
#pDiv p {
  width: 200px;
  flex-grow: 2;
}
#child {
  /* border: 1px solid red; */
  overflow: hidden;
  width: 20px;
  height: 20px;
  margin-top: 15px;
  margin-left: 0px;
  left: 10%;
  float: inline-end;
}
